import scormApi from './scorm-api-wrapper';
import jsonp from 'jsonp';
import querystring from 'querystring';

(function (global) {
	if (!global.console) {
		global.console = {};
	}
	const con = global.console;
	let prop,
		method;
	const dummy = function () {};
	const properties = ['memory'];
	const methods = (
		'assert,clear,count,debug,dir,dirxml,error,exception,group,' +
		'groupCollapsed,groupEnd,info,log,markTimeline,profile,profiles,profileEnd,' +
		'show,table,time,timeEnd,timeline,timelineEnd,timeStamp,trace,warn'
	).split(',');
	while ((prop = properties.pop())) if (!con[prop]) con[prop] = {};
	while ((method = methods.pop())) {
		if (typeof con[method] !== 'function') con[method] = dummy;
	}
	// Using `this` for web workers & supports Browserify / Webpack.
}(typeof window === 'undefined' ? this : window));

// XDomainRequest
const ajax = (url, callback, data, x) => {
	try {
		x = new (window.XMLHttpRequest || ActiveXObject)('MSXML2.XMLHTTP.3.0');
		x.open(data ? 'POST' : 'GET', url, 1);
		x.setRequestHeader('X-Requested-With', 'XMLHttpRequest');
		x.setRequestHeader('Content-type', 'application/json');
		x.onreadystatechange = function () {
			x.readyState > 3 && callback && callback(x.responseText, x);
		};
		x.send(JSON.stringify(data));
	} catch (e) {
		window.console && console.log(e);
	}
};

const jsonpajax = (url, callback, data, x) => {
	const q = querystring.encode(data);
	try {
		jsonp(`${url}?${q}`, (err, data) => {
			if (err) throw err;
			callback(data, x);
		});
	} catch (e) {
		window.console && console.log(e);
	}
};

const buildUrlEntry = () => {
	// Global integration
	if (!window.beedeezData.categoryId && !window.beedeezData.lessonId) {
		return 'home';
	}
	// lesson integration
	if (window.beedeezData.lessonId) {
		return `tracks/${window.beedeezData.trackId}/lessons/${
			window.beedeezData.lessonId
		}`;
	}
	return `categories/${window.beedeezData.categoryId}`;
};

const getMemberId = () => window.beedeezData.memberId;

const getIntegrationType = () => {
	const param = `integrationType=${
		window.beedeezData.integrationType
	}&integrationId=${window.beedeezData.lessonId ||
		window.beedeezData.categoryId}`;
	return param;
};

export const buildUrl = (token) => {
	const url = `${
		process.env.__WEBAPP_PRO_URL
	}?${getIntegrationType()}&token=${token}#/${getMemberId()}/${buildUrlEntry(
		token
	)}`;
	return url;
};

const updateRawScore = (min, max, raw) => {
	window.pipwerks.SCORM.data.set('cmi.core.score.min', min);
	window.pipwerks.SCORM.data.set('cmi.core.score.max', max);
	window.pipwerks.SCORM.data.set('cmi.core.score.raw', raw);
};

const getUserId = () =>
	window.pipwerks.SCORM.data.get('cmi.core.student_id') ||
	window.pipwerks.SCORM.data.get('cmi.learner_id');

const getUserName = () =>
	window.pipwerks.SCORM.data.get('cmi.core.student_name') ||
	window.pipwerks.SCORM.data.get('cmi.learner_name');

const log = (data) => {
	if (window.console && console.log) {
		console.log(data);
	}
};

const startLmsSession = () => {
	if (!window.parent.scormApi || !window.parent.scormApi.APIInitialized) {
		window.pipwerks.SCORM.init();
	}
};

const saveLMsSession = () => {
	window.pipwerks.SCORM.save();
};

const MillisecondsToCMIDuration = (n) => {
	// Convert duration from milliseconds to 0000:00:00.00 format
	let hms = '';
	const dtm = new Date();
	dtm.setTime(n);
	const h = `0${Math.floor(n / 3600000)}`;
	const m = `0${dtm.getMinutes()}`;
	const s = `0${dtm.getSeconds()}`;
	hms = `${h.substr(h.length - 2)}:${m.substr(m.length - 2)}:`;
	hms += s.substr(s.length - 2);
	return hms;
};

const updateSessinTime = (sessionTime) => {
	if (!sessionTime) {
		return;
	}
	window.pipwerks.SCORM.set(
		'cmi.core.session_time',
		MillisecondsToCMIDuration(sessionTime)
	);
};

const unloaded = false;
function unloadHandler() {
	finishLmsSession();
}
window.onbeforeunload = unloadHandler;
window.onunload = unloadHandler;

const finishLmsSession = () => {
	window.pipwerks.SCORM.set('cmi.core.exit', 'suspend');
	window.pipwerks.SCORM.quit();
};

const updateLessonStatus = (status) => {
	window.pipwerks.SCORM.data.set('cmi.core.lesson_status', status);
};

const handleMessage = (event) => {
	// TODO: chek event.origin
	const data = event.data;
	if (data) {
		switch (data.type) {
		case 'START_LESSON':
			startLesson(data.lesson);
			break;
		case 'UPDATE_USER_POINTS':
			updateRawScore(data.min, data.max, data.raw);
			break;
		case 'START_LMS_SESSION':
			startLmsSession();
			break;
		case 'UPDATE_SESSION_TIME':
			updateSessinTime(data.time);
			break;
		case 'SAVE_LMS_SESSION':
			saveLMsSession();
			break;
		case 'FINISH_LESSON':
			updateLessonStatus(data.status);
			break;
		case 'FINISH_LMS_SESSION':
			finishLmsSession(data.reason);
			break;
		default:
			//
		}
	}
};

const bindFrame = (iframe) => {
	window.addEventListener('message', handleMessage, false);
};

const startBeedeez = () => {
	if (!window.parent.scormApi || !window.parent.scormApi.APIInitialized) {
		window.pipwerks.SCORM.init();
	} else {
		window.pipwerks.SCORM.connection.isActive = true;
	}
	const url = `${process.env.API_ROOT}/api/v1/server/${getMemberId()}/users`;
	const data = {
		lmsServerId: window.beedeezData.memberId,
		lmsLearnerId: getUserId(),
		userName: getUserName(),
		token: window.beedeezData.token
	};
	const makeAjaxrequest = window.XDomainRequest ? ajax : ajax;
	jsonpajax(
		url,
		(response) => {
			const iframe = document.createElement('iframe');
			const responseObject =
				typeof response === 'string' ? JSON.parse(response) : response;
			iframe.style.width = '100%';
			iframe.style.border = '0';
			iframe.style.height = `${window.innerHeight}px`;
			log(buildUrl(responseObject.token));
			iframe.src = buildUrl(responseObject.token);
			bindFrame(iframe);
			document.body.appendChild(iframe);
		},
		data
	);
};

startBeedeez();



